body{background-color: #eff3f7;}
section{
  display: grid;
  grid-template-columns: repeat(2,minmax(400px,1fr));
  grid-gap: 100px;
  width: 80%;
  padding: 70px 50px 50px;
  margin: 0 auto;
}
article{
  /* width: 300px; height: 260px; */
  /* border: 3px solid #f00; */
  box-shadow: 0 5px 8px rgba(0,0,0,.4);
  margin: 30px;
  background-color: #fff;
  margin-bottom: 70px;
  padding: 20px 20px 40px 20px;
  border-radius: 5px;
  display: grid;
  grid-template-columns: minmax(150px,1fr) 2fr;
  grid-gap: 20px;
  font-weight: 300;
}
article img{
  position: relative;
  top: -50px;
  width: 150px;
  box-shadow: 0 8px 25px rgba(0,0,0,.3);
}
article h3{font-weight: 300; font-size: 24px;}
article p{color: rgb(170, 170, 170);}